<template>
  <div>
    <div class="container my-5">
      <h2 class="text-center text-primary">聯絡資訊</h2>
      <div class="form-row pt-3">
        <div class="input-group col-2">
          <input type="text" class="form-control" v-model="contact.category" />
        </div>
        <div class="input-group col-3">
          <input type="text" class="form-control" v-model="contact.origin_price" />
        </div>
        <div class="input-group col-6">
          <input type="text" class="form-control" v-model="contact.title" />
        </div>
        <button class="btn btn-primary col-1" @click.prevent="addContact">新增</button>
      </div>
      <div class="form-row py-3">
        <div class="input-group col-2">
          <select class="form-control" v-model="filter">
            <option value="category">姓名</option>
            <option value="origin_price">聯絡方式</option>
            <option value="title">地址</option>
          </select>
        </div>
        <div class="input-group col-10">
          <input type="text" class="form-control" v-model="search" />
          <div class="input-group-append">
            <button class="btn btn-primary">
              <i class="fas fa-search"></i>
            </button>
          </div>
        </div>
      </div>
      <table class="table table-hover text-dark mb-5">
        <thead class="bg-secondary">
          <tr>
            <th width="120px">姓名</th>
            <th width="180px">聯絡方式</th>
            <th>地址</th>
            <th width="120px">編輯</th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(item, key) in searchData" :key="key" @dblclick="getedit(item)">
            <td>
              <span v-if="editcontact != item">{{ item.category }}</span>
              <input
                type="text"
                class="form-control"
                v-if="editcontact == item"
                v-model="isName"
                @keyup.esc="escedit"
                @keyup.enter="doneedit(item)"
              />
            </td>
            <td>
              <span v-if="editcontact != item">{{ item.origin_price }}</span>
              <input
                type="text"
                class="form-control"
                v-if="editcontact == item"
                v-model="isPhone"
                @keyup.esc="escedit"
                @keyup.enter="doneedit(item)"
              />
            </td>
            <td>
              <span v-if="editcontact != item">{{ item.title }}</span>
              <input
                type="text"
                class="form-control"
                v-if="editcontact == item"
                v-model="isAddress"
                @keyup.esc="escedit"
                @keyup.enter="doneedit(item)"
              />
            </td>
            <td>
              <div class="btn-group">
                <!-- <button class="btn btn-sm btn-primary" @click.prevent="editbtn(item)">編輯</button> -->
                <button class="btn btn-sm btn-danger" @click.prevent="delContacts(item.id)">刪除</button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contactData: [],
      contact: {
        category: " ",
        origin_price: " ",
        title: " "
      },
      pagination: [],
      editcontact: {},
      filter: "category",
      search: "",
      isName: "",
      isPhone: "",
      isAddress: "",
      edit: false
    };
  },
  methods: {
    getContacts() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/admin/products/all`;
      vm.$http.get(api).then(response => {
        var products = response.data.products;
        vm.contactData = Object.values(products);
        console.log(vm.contactData);
      });
    },
    addContact() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/admin/product`;
      vm.$http.post(api, { data: vm.contact }).then(response => {
        console.log(response.data);
        vm.getContacts();
        vm.contact = [];
      });
    },
    delContacts(id) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/admin/product/${id}`;
      vm.$http.delete(api).then(response => {
        console.log(response.data);
        vm.getContacts();
      });
    },
    getedit(item) {
      if (this.editcontact == item) {
        this.editcontact = {};
      } else {
        this.editcontact = item;
        this.isName = this.editcontact.category;
        this.isPhone = this.editcontact.origin_price;
        this.isAddress = this.editcontact.title;
      }
    },
    escedit() {
      this.editcontact = {};
    },
    doneedit(item) {
      const vm = this;
      item.category = vm.isName;
      item.origin_price = vm.isPhone;
      item.title = vm.isAddress;
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/admin/product/${item.id}`;
      vm.$http.put(api, { data: item }).then(response => {
        console.log(response.data);
        vm.getContacts();
      });
      this.editcontact = {};
    },
    editbtn(item) {
      const vm = this;
      let isClick = true;
      if (isClick) {
        isClick = false;
        console.log(isClick);
        if (vm.editcontact != item) {
          vm.getedit(item);
        } else {
          vm.doneedit(item);
        }
        var deley = setTimeout(() => {
          isClick = true;
        }, 1000);

        clearTimeout(deley);
      } else {
        return false;
      }
    },
    check() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}logout`;
      this.$http.post(api).then(response => {
        console.log(response.data);
      });
    }
  },
  computed: {
    searchData() {
      const vm = this;
      return vm.contactData.filter(item => {
        if (item[vm.filter]) {
          if (item[vm.filter].indexOf(vm.search) >= 0) {
            return item;
          }
        } else {
          return item;
        }
      });
    }
  },
  created() {},
  mounted() {
    this.getContacts();
  }
};
</script>